body {
  margin: 0;
  padding: 0;
}

#root {
  background: #f9f9f9;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
}
@font-face {
  font-family: "Open Sans";
  src: url(/stagit/static/media/OpenSans-Regular.629a55a7.ttf);
}
@font-face {
  font-family: "Open Sans Light";
  src: url(/stagit/static/media/OpenSans-Light.1bf71be1.ttf);
}


/* ~~~~~~~~~Global Selectors~~~~~~~~~~ */

/* #1277eb  blue */
/* #1ba94c green */
/* #6c5ce7 main */
/* #6e3acb hover */

.container-60 {
  width: 60%;
  margin: 0 auto;
}
.container-50 {
  width: 50%;
  margin: 0 auto;
}

.container-75 {
  width: 75%;
  margin: 0 auto;
}

.container-40 {
  width: 40%;
  margin: 0 auto;
}


.neutral-link {
  text-decoration: none;
  color: inherit;
}

.link-hover:hover {
  color: #fff;
}

/* ~~~~~~~~~Content Specific Selectors~~~~~~~~~~ */

.Home, .Support, .About, .User {
  padding-top: 80px;
  box-sizing: border-box;
  color: #333;
}

/* ~~~~~~~~~App Component~~~~~~~~~~ */
.App {
  background: #fff;
  font-family: "Karla";
  box-shadow: 0 0 10px 5px rgba(0,0,0,0.2);
  height: 100vh;
}

.App > p {
  margin: 0;
}

/* ~~~~~~~~~NavBar Component~~~~~~~~~~ */
.NavBar {
  color: #eee;
  position: fixed;
  top: 0;
  height: 80px;
  width: 100vw;
  background: #6c5ce7;
  box-shadow: 0 3px 6px 0 rgba(0,0,0,0.24), 0 3px 8px 1px rgba(0,0,0,0.16), 0 3px 10px 5px rgba(0,0,0,0.1);
  /* box-shadow: 0 3px 4px 0 rgba(0,0,0,0.3), 0 3px 3px -2px rgba(0,0,0,0.16), 0 1px 8px 0 rgba(0,0,0,0.1); */
  z-index: 5;
}

.nav-container {
  width: 80%;
  margin: 0 auto;
  height: inherit;
  display: flex;
  justify-content: space-between;
}

/* ~~~~~~~~~NavBar NavBrand Component~~~~~~~~~~ */
.NavBrand {
  display: flex;
  align-items: center;
  height: inherit;
}

.nav-brand-img {
  height: 30px;
}

.nav-brandname {
  padding: 0 5px 0 15px;
  font-size: 30px;
  font-family: "Segoe UI";
  font-weight: 800;
}

/* ~~~~~~~~~NavBar NavPanel Component~~~~~~~~~~ */
.NavPanel {
  height: inherit;
}
.nav-list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  align-items: center;
  height: inherit;
}

.nav-list-item {
  margin: 0 20px;
  font-size: 18px;
  text-transform: uppercase;
  font-weight: 400;
}

/* ~~~~~~~~~Home Component~~~~~~~~~~ */
.Home {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  font-family: "Karla";
}

.form-user {
  /* background: grey; */
}

.form-user > * {
    display: block;
    width: 500px;
    margin: 20px 0;
}

.form-user label {
  margin-bottom: 5px;
  color: #666;
  font-size: 18px;
}

.form-input-username {
  margin-top: 0;
  padding: 20px;
  height: 60px;
  font-family: "Montserrat";
  font-size: 16px;
  border-radius: 5px;
  border: none;
  background: #f4f4f4;
  box-sizing: border-box;
}

.form-input-username:focus {
  background: #fff;
  border-radius: 10px !important;
}

.form-submit-user {
  /* background: linear-gradient(#674aa3, #6C5CE7); */
  background: #6c5ce7;
  padding: 0;
  /* border: 1px solid #6C5CE7; */
  border: none;
  height: 50px;
  text-transform: uppercase;
  font-family: "Montserrat";
  font-weight: bold;
  color: #fff;
  font-size: 20px;
  border-radius: 5px;
  cursor: pointer;
}

.form-submit-user:hover {
  background: #6e3acb;
}

.form-warning {
  color: red;
  font-weight: 300;
  font-family: "Karla";
  text-align: center;
}

/* ~~~~~~~~~About Component~~~~~~~~~~ */
.About {
  text-align: center;
}

.about-content {
  margin-top: 100px;
  font-family: "Open Sans Light";
  color: #444;
}

.about-img {
  height: 60px;
  margin-right: 10px;
  position: relative;
  top: 5px;
}

.about-header {
  font-family: "Segoe UI";
  font-weight: 800;
  font-size: 60px;
  margin-right: 20px;
}

/* ~~~~~~~~~User Component~~~~~~~~~~ */

/* ~~~~~~~~~Sidebar Component~~~~~~~~~~ */
.Sidebar {
  position: fixed;
  left: 0;
  width: 60px;
  height: 100vh;
  background: #6c5ce7;
}

.sidebar-nav {
  list-style: none;
  padding: 0;
  margin: 0;
}

.sidebar-item {
  padding: 15px 0;
  transition: 150ms;
  width: inherit;
  cursor: pointer;
}

.sidebar-item:not(.active):hover {
  background: #6c5ce7;
  -webkit-filter: brightness(0.8);
          filter: brightness(0.8);
  transition: 300ms;
}

.sidebar-item.active {
  background: #6e3acb;
  box-shadow: 0 10px 10px -3px rgba(0,0,0,0.15) inset, 0 -10px 10px -3px rgba(0,0,0,0.15) inset;
  position: relative;
  z-index: 5;
}

.sidebar-item-img {
  display: block;
  width: 30px;
  margin: 0 auto;
}


